.full-page-image {
  width: var(--pagedjs-width);
  height: var(--pagedjs-height);
  overflow: hidden;
  left: calc((var(--pagedjs-bleed-left) + var(--pagedjs-margin-left)) * -1);
  top: calc((var(--pagedjs-bleed-top) + var(--pagedjs-margin-top)) * -1);
  position: absolute;
}

.full-page-image div {
  width: calc(var(--pagedjs-width) * 2 - var(--pagedjs-bleed-left) - var(--pagedjs-bleed-right));
  height: var(--pagedjs-height);
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

/* compensate for bleeds */
.pagedjs_right_page .full-page-image div {
  margin-left: calc(-1 * var(--pagedjs-width) + var(--pagedjs-bleed-left) + var(--pagedjs-bleed-right));
}

.full-page-image img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  margin: 0 !important;
}

.title-spread .full-page-image {
  background-color: black;
}

.full-spread-image-section .full-page-image div.full-spread-image-caption {
  height: auto;
  position: absolute;
  display: block;
  right: calc(var(--pagedjs-bleed-right) + var(--pagedjs-margin-right));
  bottom: calc(var(--pagedjs-bleed-bottom) + var(--pagedjs-margin-bottom));
  width: calc(var(--pagedjs-width) - var(--pagedjs-bleed-right) - var(--pagedjs-margin-right) - var(--pagedjs-bleed-left) - var(--pagedjs-margin-left));
}

:root {
  --rendering-stage: "page";
  --loader-color: #5A6A5C;
}

.loading-message {
  position: absolute;
  display: inline-block;
  top: 50px;
  left: 50%;
  transform: translateX(-110%);
  margin: 0 auto;
  padding: 10px;
  border: 2px solid var(--loader-color);
  border-radius: 10px;
  text-align: center;
  color: var(--loader-color);
  z-index: 1000;
  background-color: rgba(255, 255, 255, 0.3);
}

.loading-message:after {
  content: var(--rendering-stage) " " var(--pages-rendered);
}

.lds-heart {
  display: block;
  position: relative;
  width: 80px;
  height: 80px;
  transform: rotate(45deg);
  transform-origin: 40px 40px;
  margin: 0 auto;
}

.lds-heart div {
  top: 32px;
  left: 32px;
  position: absolute;
  width: 32px;
  height: 32px;
  background: var(--loader-color);
  animation: lds-heart 1.2s infinite cubic-bezier(0.215, 0.61, 0.355, 1);
}

.lds-heart div:after,
.lds-heart div:before {
  content: " ";
  position: absolute;
  display: block;
  width: 32px;
  height: 32px;
  background: var(--loader-color);
}

.lds-heart div:before {
  left: -24px;
  border-radius: 50% 0 0 50%;
}

.lds-heart div:after {
  top: -24px;
  border-radius: 50% 50% 0 0;
}

@keyframes lds-heart {
  0% {
    transform: scale(0.95);
  }
  5% {
    transform: scale(1.1);
  }
  39% {
    transform: scale(0.85);
  }
  45% {
    transform: scale(1);
  }
  60% {
    transform: scale(0.95);
  }
  100% {
    transform: scale(0.9);
  }
}
.print-ui {
  position: fixed;
  top: 0;
