  display: inline-block;
  vertical-align: middle;
  z-index: 20;
}

.menu__icon span {
  display: block;
  background: black;
  width: 100%;
  height: 2px;
  margin-top: -1px;
  position: absolute;
  left: 0;
  top: 50%;
}

.menu__icon:before,
.menu__icon:after {
  content: "";
  display: block;
  background: black;
  width: 100%;
  height: 2px;
  position: absolute;
  left: 0;
  transform-origin: center center;
  transform: rotate(0deg);
  transition: all 0.3s ease;
}

.menu__icon:before {
  top: 1px;
  margin-top: -1px;
}

.menu__icon:after {
  bottom: 1px;
  margin-bottom: -1px;
}

.show_hide {
  display: none;
}

.show_hide:checked ~ label .menu__icon span {
  background: transparent;
}

.show_hide:checked ~ label .menu__icon:before {
  top: 50%;
  transform: rotate(45deg);
}

.show_hide:checked ~ label .menu__icon:after {
  bottom: 50%;
  transform: rotate(-45deg);
}

.hide_foreground .pagedjs_pagebox *, .hide_foreground .pagedjs_pagebox *::after, .hide_foreground .pagedjs_pagebox *::before, .hide_foreground .pagedjs_pagebox a {
  color: transparent !important;
}
.hide_foreground .pagedjs_pagebox img {
  opacity: 0;
}

.hide_background .pagedjs_pagebox * {
  background-color: transparent !important;
}
.hide_background .p5Canvas {
  display: none !important;
}

/* General styling */
html {
  font-size: 62.5%;
}

body {
  color: var(--color-main);
  margin: 0;
  padding: 0;
}

img {
  width: auto;
  height: auto;
  max-width: 85%;
  max-height: 80%;
  border-radius: 1rem;
  display: block;
  margin: 12pt auto;
}

h3 + p {
  text-indent: 0 !important;
}

.pagedjs_pages .chapter.invert-type h2 + p {
  text-indent: 0 !important;
}

/*
Setting named-pages
https://pagedjs.org/documentation/8-named-page/
*/
#cover {
  page: toc;
  list-style: none;
}

h1 {
  page: chapter-title;
}

.empty-page {
  page: empty-page !important;
}

.title-page {
  page: chapter-title;
}

/* Prevent superscript from messing up the line-height */
sup {
  vertical-align: top;
  position: relative;
  top: -0.5em;
}

a {
  text-decoration: none;
  color: black;
  overflow-wrap: break-word;
  word-wrap: break-word;
  word-break: break-word;
}

/* Generic css */
/* Show url of a tags */
/* a::after {
content: " (" attr(href) ")";
} */
a.image {
  display: block;
  text-align: center;
}

.pagedjs_pages .pagedjs_area .chapter .image-container {
  margin: 0;
  padding: 0;
  text-indent: 0;
  text-align: center;
}
.pagedjs_pages .pagedjs_area .chapter .image-container a {
  display: block;
}

/* 
Page margin colors. The color names are specified in sketch.js
Available color: red, green, brown
*/
@page chapter-title {
  @top-center {
    content: "";
  }
  @bottom-center {
    content: "";
  }
  @top-left {
    content: "";
  }
  @top-right {
    content: "";
  }
}
/* 
	Change the display of these margin elements to inline-block
	so they have a meaningful width for use with the P5js sketch.
*/
.pagedjs_pages .pagedjs_margin-top-center .pagedjs_margin-content,
.pagedjs_pages .pagedjs_margin-bottom-center .pagedjs_margin-content {
  display: inline-block;
  width: auto !important;
  margin: 0 auto;
}

/* For placement op p5js sketch relative to the page */
.pagedjs_page, .pagedjs_pagebox {
  position: relative;
}

/* First page */
.book-title {
  font-family: Ortica, serif;
  font-weight: 300;
  font-size: 14pt;
  line-height: 15pt;
}

/* thumbnails, keep caption and image on same page */
.thumb {
  break-inside: avoid;
}

.thumbinner {
  margin: 0 auto;
}

/* mediawiki gives thumbs an inline fixed width */
.thumbinner.image-container {
  width: auto !important;
}

mark {
  background-color: transparent;
}

/* for debugging */
.grid mark {
  background-color: rgba(255, 255, 0, 0.3);
}

mark:after {
  font-family: "Symbola", sans-serif;
  font-size: 10pt;
  line-height: 0;
}

.c1:after {
  content: "⇢";
}

.c2:after {
  content: "⇛";
}

.c3:after {
  content: "⟴";
}

.c4:after {
  content: "↦";
}

.c5:after {
  content: "↬";
}

.c6:after {
  content: "↣";
}

.c7:after {
  content: "⇝";
}